<template>
  <Form :model="queryForm" ref="queryForm" :label-width="80">
    <Row :gutter="16">
      <Col span="6">
        <FormItem label="姓名:" prop="realName">
          <Input v-model="queryForm.realName" placeholder="请输入姓名"></Input>
        </FormItem>
      </Col>
      <Col span="6">
        <FormItem label="互市区:">
          <Cascader
            :data="data"
            @on-change="regionChange"
            v-model="customs"
            placeholder="请选择互市"
          ></Cascader>
        </FormItem>
      </Col>
      <Col span="6">
        <FormItem label="手机号" prop="phone">
          <Input v-model="queryForm.phone" placeholder="请输入手机号"></Input>
        </FormItem>
      </Col>
      <Col span="6">
        <FormItem label="身份证:" prop="idCode">
          <Input
            v-model="queryForm.idCode"
            placeholder="请输入身份证号码"
          ></Input>
        </FormItem>
      </Col>
      <!-- <Col span="6">
        <FormItem label="用户类型:">
          <Input v-model="queryForm.type" placeholder="请选择用户类型"></Input>
        </FormItem>
      </Col> -->

      <Col span="24">
        <Row justify="end" type="flex">
          <Col span="12">
            <FormItem class="form-btn">
              <Button type="primary" @click="query">查询</Button>
              <Button style="margin-left: 8px" @click="reset">重置</Button>
            </FormItem>
          </Col>
        </Row>
      </Col>
    </Row>
  </Form>
</template>
<script>
/**
 * 查询用户列表 表单组件
 */
import { REGION } from '@/data/region.js'
export default {
  name: 'query-form',
  data() {
    return {
      queryForm: {
        realName: '',
        idCode: '',
        phone: '',
        startDate: '',
        endDate: '',
        region: '',
        civil: '',
        page: 1,
        size: 20
      },
      customs: [],
      data: REGION
    }
  },
  created() {
    this.$emit('query', this.queryForm) //页面是否重新加载数据
  },
  activated() {
    if (this.$store.state.userStatus) {
      this.$emit('query', this.queryForm) //页面是否重新加载数据
      this.$store.state.userStatus = false
    }
  },
  methods: {
    query() {
      this.queryForm.page = 1
      //向父组件 发射数据
      this.$emit('query', this.queryForm)
    },
    reset() {
      this.customs = []
      this.queryForm.region = ''
      this.$refs.queryForm.resetFields()
    },
    regionChange(value) {
      this.queryForm.region = value[1]
    }
  }
}
</script>
<style lang="scss" scoped>
.form-btn {
  display: flex;
  justify-content: flex-end;
}
</style>
