<template>
  <div>
    <Col span="24" ref="toolsBar" class-name="layout-table-tools">
      <Button type="primary" @click="add('user')"
        ><Icon type="md-add-circle" /> 添加用户</Button
      >
      <Button type="primary" @click="add('super')"
        ><Icon type="md-add-circle" /> 添加超管</Button
      >
      <Button type="primary" @click="add('civil')"
        ><Icon type="md-add-circle" /> 添加边民</Button
      >
    </Col>
    <Modal
      v-model="addModal"
      :title="'添加' + modalTitle"
      loading
      @on-cancel="cancel"
    >
      <Form
        :model="userForm"
        ref="addFrom"
        :label-width="80"
        :rules="addUserRlue"
      >
        <FormItem label="手机号" prop="phone">
          <Input v-model="userForm.phone" placeholder="请输入手机号"></Input>
        </FormItem>
        <FormItem label="身份证" prop="idCode" v-if="modalTitle == '边民'">
          <Input v-model="userForm.idCode" placeholder="请输入身份证"></Input>
        </FormItem>
        <FormItem label="密码" prop="password" v-else>
          <Input
            type="password"
            v-model="userForm.password"
            placeholder="请输入密码"
          ></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="cancel">取消</Button>
        <Button type="primary" :loading="modal_loading" @click="save('addFrom')"
          >保存</Button
        >
      </div>
    </Modal>
  </div>
</template>
<script>
/**
 * 用户列表 工具栏目
 *
 */
import { addUserCivil, addUser, addUserSuper } from '@/api/user'
import { SERVER_CODE } from '@/utils/constants.js'
export default {
  data() {
    return {
      addUserRlue: {
        phone: [
          {
            required: true,
            message: '请输入手机号',
            trigger: 'blur'
          }
        ],
        idCode: [
          {
            required: true,
            message: '请输入身份证号',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            type: 'string',
            min: 6,
            message: '密码长度不能小于6位数',
            trigger: 'blur'
          }
        ]
      },
      addModal: false,
      userForm: {
        phone: '',
        password: '',
        idCode: ''
      },
      modalTitle: '',
      modalFlag: '',
      modal_loading: false
    }
  },
  methods: {
    add(flag) {
      this.modalFlag = flag
      switch (flag) {
        case 'user':
          this.modalTitle = '用户'
          break
        case 'super':
          this.modalTitle = '超管'
          break
        case 'civil':
          this.modalTitle = '边民'
          break
      }
      this.$refs.addFrom.resetFields()
      this.addModal = true
    },
    save(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.handlerAddUser()
        }
      })
    },
    /**
     * 处理添加用户
     */
    async handlerAddUser() {
      switch (this.modalFlag) {
        case 'user':
          await addUser(this.userForm)
            .then(({ msg, code }) => {
              if (code === SERVER_CODE.CODE_200) {
                this.$Message.success('用户添加成功!')
              } else {
                this.$Message.info(msg)
              }
            })
            .catch(error => {
              console.log(error)
            })
            .finally(() => {})
          break
        case 'super':
          await addUserSuper(this.userForm)
            .then(({ msg, code }) => {
              if (code === SERVER_CODE.CODE_200) {
                this.$Message.success('超管添加成功!')
              } else {
                this.$Message.info(msg)
              }
            })
            .catch(error => {
              console.log(error)
            })
            .finally(() => {})
          break
        case 'civil':
          await addUserCivil(this.userForm)
            .then(({ msg, code }) => {
              if (code === SERVER_CODE.CODE_200) {
                this.$Message.success('边民添加成功!')
              } else {
                this.$Message.info(msg)
              }
            })
            .catch(error => {
              console.log(error)
            })
            .finally(() => {})
          break
      }
      setTimeout(() => {
        this.addModal = false
      }, 2000)
    },
    cancel() {
      this.addModal = false
    }
  }
}
</script>
<style lang="scss" scoped>
.layout-table-tools {
  button {
    margin-right: 12px;
  }
}
</style>
