<template>
  <Card :bordered="false" dis-hover>
    <query-form ref="searchDiv" @query="queryEvent" />
    <Row>
      <tools-bar ref="toolsBar" />
      <Col span="24" class-name="layout-table-list">
        <Table
          :size="tableOpts.size"
          :height="tableOpts.height"
          ref="table"
          :loading="loading"
          :columns="userTableCol"
          :data="userTableData"
        >
          <template slot-scope="{ row, index }" slot="action">
            <Button
              type="primary"
              size="small"
              style="margin-right: 5px"
              @click="info(index)"
              >查看</Button
            >
            <Button
              type="primary"
              size="small"
              style="margin-right: 5px"
              @click="edit(index)"
              >编辑</Button
            >
            <Button type="error" size="small" @click="remove(index)"
              >删除</Button
            >
          </template></Table
        >
      </Col>
      <Col span="24" class-name="layout-table-pages">
        <Page
          show-total
          :total="pageTotal"
          @on-change="pageChange"
          :page-size="20"
          :page-size-opts="[20, 30, 40]"
          @on-page-size-change="pageSizeChange"
          show-sizer
        />
      </Col>
    </Row>
  </Card>
</template>
<script>
import { SERVER_CODE } from '@/utils/constants.js'
import { userList, userDelete } from '@/api/user.js'
import QueryForm from './components/QueryForm'
import ToolsBar from './components/ToolsBar'
import { getRegionName } from '@/utils/common.js'
import { spinShowMixins } from '@/mixins/spin-mixins.js'
export default {
  components: {
    QueryForm,
    ToolsBar
  },
  mixins: [spinShowMixins],
  data() {
    return {
      userTableCol: [
        {
          title: 'ID',
          key: 'id',
          sortable: true
        },
        {
          title: '姓名',
          key: 'realName'
        },
        {
          title: '所属互市',
          key: 'customs',
          render: (h, params) => {
            return h('span', getRegionName(params.row))
          }
        },
        {
          title: '手机号',
          key: 'phone'
        },
        {
          title: '证件号',
          key: 'idCode'
        },
        {
          title: '是否完成边民认证',
          key: 'civil',
          align: 'center',
          render: (h, params) => {
            const row = params.row
            const color = row.civil ? 'success' : 'error'
            const text = row.civil ? '是' : '否'

            return h(
              'Tag',
              {
                props: {
                  type: 'dot',
                  color: color
                }
              },
              text
            )
          }
        },
        {
          title: '操作',
          slot: 'action',
          width: 200,
          align: 'center',
          fixed: 'right'
        }
      ],
      userTableData: [],
      queryParams: {},
      pageTotal: 0,
      loading: true
    }
  },
  methods: {
    queryEvent(params) {
      this.queryParams = params
      this.search()
    },
    search() {
      this.loading = true
      userList(this.queryParams)
        .then(({ code, data, msg }) => {
          if (code === SERVER_CODE.CODE_200) {
            this.pageTotal = data.count
            this.userTableData = data.content
            this.loading = false
          } else {
            this.$Message.info(msg)
            this.loading = false
          }
        })
        .catch(error => {
          console.log(error)
          this.loading = false
        })
        .finally(() => {
          this.loading = false
        })
    },
    pageChange(page) {
      this.queryParams.page = page
      this.search()
    },
    pageSizeChange(size) {
      this.queryParams.size = size
      this.search()
    },
    edit(index) {
      //用户编辑
      let id = this.userTableData[index].id
      this.$router.push({ name: 'user-edit', query: { userId: id } })
    },
    info(index) {
      // 用户详情
      let id = this.userTableData[index].id
      this.$router.push({ name: 'user-show', query: { userId: id } })
    },
    remove(index) {
      //删除一条数据
      this.$Modal.confirm({
        title: '确认删除',
        content: '<p>该操作将导致数据无法恢复，请谨慎操作！</p>',
        onOk: () => {
          let params = { id: this.userTableData[index].id }
          userDelete(params)
            .then(({ code, msg }) => {
              if (code === SERVER_CODE.CODE_200) {
                this.$Message.success('删除成功！')
                this.userTableData.splice(index, 1)
                this.selectList = [] //清空单选框选择数据
                this.count--
              } else {
                this.$Message.info(msg)
                console.log('应用删除失败')
              }
            })
            .catch(error => {
              console.log(error)
            })
            .finally(() => {})
        },
        onCancel: () => {}
      })
    }
  }
}
</script>
